import {
	mapState,
	mapMutations
} from 'vuex';
import store from "../../../store/index.js";
import api from "../../../api/api.js";
export default {
	computed: mapState(['hasLogin', 'uerInfo', 'token']),
	data() {
		return {
			submitDisabled:true,
			wordCount: 0,
			sendDate: {
				content: ''
			}
		};
	},
	onLoad() {},
	methods: {
		goBack(){
			uni.navigateBack({
				delta: 1
			})
		},
		descInput(){
			this.wordCount = this.sendDate.content.length;
			if(this.sendDate.content.length >= 10){
				this.submitDisabled=false;
			}else{
				this.submitDisabled=true;
			}
		},
		send(){
			if (!this.hasLogin) {
				uni.showModal({
					content: "请先登录",
					success: function (res) {
						if (res.confirm) {
							uni.navigateTo({
								url: '../login/login'
							})
						} else if (res.cancel) {
							return;
						}
					}
				});
			}else{
				var url = api.urls.feedback;
				var data = { feedbackContent:this.sendDate.content };
				api.req.post(url, data, (res) => {
					if(res.code===0){
						uni.showModal({
							content: "反馈成功！",
							showCancel: false
						})
						uni.navigateBack();
					}else{
						uni.showModal({
							content: "反馈失败！",
							showCancel: false
						})
						return;
					}
				});
			}
		},
		...mapMutations(['login'])
	}
};
